import React, { useState } from 'react';

const Subscribe: React.FC = () => {
  const [email, setEmail] = useState('');
  const [ok, setOk] = useState(false);
  const submit = (e: any) => { e.preventDefault(); setOk(true); };
  return (
    <div>
      <h2>Chci dostvat novinky</h2>
      {ok ? <p>Dkujeme! Brzy vs budeme informovat.</p> : (
        <form onSubmit={submit}>
          <input type="email" placeholder="V e-mail" value={email} onChange={e => setEmail(e.target.value)} />
          <button className="button" type="submit">Pihlsit se</button>
        </form>
      )}
    </div>
  );
};

export default Subscribe;
